html {
  font-family: "Manrope", sans-serif;
  scroll-behavior: smooth;
}

.main {
  background-color: $main-white-color;
  &__content {
    display: grid;
    row-gap: 96px;
    padding-block-end: 96px;
    padding-block-start: 64px;

    @include onTablet {
      padding-block: 120px;
      row-gap: 120px;
    }
  }
}
